<template>
    <div>
        <b-card class="text-center m-3">
            <strong>Total Block Entities:</strong> {{entities.length}}
        </b-card>

        <b-pagination
                v-model="currentPage"
                :total-rows="entities.length"
                :per-page="rowsShown"
                align="center"
                limit="10"
        ></b-pagination>

        <b-table :items="entities" :fields="fields" striped :per-page="rowsShown" :current-page="currentPage"></b-table>
    </div>
</template>

<script>
    export default {
        name: 'BlockEntities',
        data()
        {
            return {
                rowsShown: 50,
                currentPage: 1,
                fields: [
                    {
                        label: 'Type',
                        key: 'type',
                        sortable: true
                    },
                    {
                        label: 'X',
                        key: 'x'
                    },
                    {
                        label: 'Y',
                        key: 'y'
                    },
                    {
                        label: 'Z',
                        key: 'z'
                    }
                ]
            }
        },
        computed: {
            level()
            {
                return this.$store.state.levels[this.$route.params.world];
            },
            entities()
            {
                return this.level.blockEntities.map((v) =>
                {
                    v.x = Number(v.x).toFixed(1);
                    v.y = Number(v.y).toFixed(1);
                    v.z = Number(v.z).toFixed(1);
                    return v;
                });
            }
        }
    };
</script>

